@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

html, body {
	font-family: 'Montserrat';
	font-weight: 300;
	padding: 0;
	margin: 0;
	height: 100%;
}

nav {
	text-align: center;
}

img {
	max-width: 1000px;
	width: 100%;
}

.header {
  width: 100%;
  text-align: right;
  position: relative;
  background-color: #F9F8F4;
  
  .logo {
    position: absolute;
    top: 0;
    left: 0;
    font-size: 16px;
    font-weight: 100;
    padding: 0px 0;
	margin: 0px 100px;
	text-transform: uppercase;

  }
}

.link {
  display: inline-block;
  padding: 20px 0;
  margin: 20px 15px;
  position: relative;
  text-decoration: none;
  color: #0f0f0f;
  font-weight: 800;
  font-size: 14px;

  
  .bar {
    width: 100%;
    display: inline-block;
    border-bottom: 2px solid #E2DDCA;
    position: absolute;
    bottom: 0;
    left: 0;
    opacity: 0;
    transition: all 60ms;
  }
  
  &:hover {
    .bar {
      opacity: 1;
      bottom: 15px;
    }
  }
}

#page-container {
  position: relative;
  min-height: 100vh;
}

#content-wrap {
  padding-bottom: 2.5rem;    /* Footer height */
  max-width: 800px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 27px;
  padding-right: 27px;
  padding-top: 25px;
}

#footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;            /* Footer height */
  background-color: black;
  text-align: center;
}